const getGenres = (genres) => {
  const list = data.genres;
  let genresarr = [];
  genres.map(genre => {
    list.map(item => {
      if(genre == item.id) {
        genresarr.push(item.name);
      } 
    });
  });
  return genresarr.join(', ');
};


